import React from 'react'
import '../css/globals.css'
import Link from 'gatsby-link'
import FaAlignJustify from 'react-icons/lib/fa/align-justify'
var FaHeartO = require('react-icons/lib/fa/heart-o');

function NavPage (props) {
  return (
    <div className='navbar'>
      <Link to= '/' onClick={props.toggle} className='link'> HOME </Link>
      <Link to='/projects' onClick={props.toggle} className='link'> PROJECTS </Link>
      <Link to='/blog' onClick={props.toggle} className='link'> BLOG </Link>
    </div>
  )
}

class Nav extends React.Component {
  constructor(props) {
    super (props);
    this.state = {
      renderNav : false
    }
    this.toggleNav = this.toggleNav.bind(this);
    this.close = this.close.bind(this);
  }
  toggleNav () {
    if(this.state.renderNav === false){
      this.setState(function(){
        return {
          renderNav : true
        }
      })
    } else {
      this.setState(function(){
        return {
          renderNav : false
        }
      })
    }
  }
  close () {
    if (this.state.renderNav === true) {
      this.setState(function () {
        return {
          renderNav: false
        }
      })
    }
  }
  render() {
    return (
      <div className='links' onClick={this.close}>
        <div className='linkClick' onClick={this.toggleNav}></div>
        {this.state.renderNav === true ? <NavPage toggle={this.toggleNav}/> : null}
      </div>
    )
  }
};

export default Nav


